import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/Auth.css';
import { registerUser } from '../Utils/api';

function Register() {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [passwordError, setPasswordError] = useState(''); // For password validation error
  const [loading, setLoading] = useState(false); // Add loading state
  const navigate = useNavigate();

  // Validate the password
  const validatePassword = (password) => {
    const minLength = 8;
    const regex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/; // At least one letter, one number, minimum 8 characters
    if (password.length < minLength) {
      return `Password must be at least ${minLength} characters long.`;
    }
    if (!regex.test(password)) {
      return 'Password must include at least one letter and one number.';
    }
    return ''; // No error
  };

  const handleRegister = async (e) => {
    e.preventDefault();

    // Validate the password before submitting
    const passwordValidationError = validatePassword(password);
    if (passwordValidationError) {
      setPasswordError(passwordValidationError);
      return;
    }

    setLoading(true);
    setError(''); // Clear previous errors
    setPasswordError('');

    try {
      await registerUser({ username, email, password }); // API call
      alert('Registration successful! Please log in.');
      navigate('/login'); // Redirect to login page
    } catch (err) {
      setError(err.response?.data?.message || 'Registration failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <form onSubmit={handleRegister}>
        <h2>Register</h2>
        {error && <p className="error-text alert alert-danger">{error}</p>}
        <div className="form-group">
          <label>Username:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
            className="form-control"
          />
        </div>
        <div className="form-group">
          <label>Email:</label>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            className="form-control"
          />
        </div>
        <div className="form-group">
          <label>Password:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            className="form-control"
          />
          {passwordError && <p className="error-text">{passwordError}</p>}
        </div>
        <button
          type="submit"
          className="btn btn-primary w-100 mt-3"
          disabled={loading} // Disable button during registration
        >
          {loading ? 'Registering...' : 'Register'}
        </button>
        <p className="text-center mt-3">
          Already have an account?{' '}
          <span onClick={() => navigate('/login')} className="link">
            Login
          </span>
        </p>
      </form>
    </div>
  );
}

export default Register;
